            <!--<div style="position:relative">-->
            <!--<v-text-field-->
            <!--v-if="head.filter_visible"-->
            <!--append-icon="mdi-close"-->
            <!--prepend-icon="mdi-magnify"-->
            <!--@click:append="head.filter_visible = false"-->
            <!--@click:prepend="head.filter_visible = false"-->
            <!--&gt;</v-text-field>-->

            <!--{-->
            <!--"text": "",-->
            <!--"sortable": false,-->
